import React, { useEffect, useState } from 'react';
import { Button, Card, Input, Table, Space, Divider, Avatar, Image, Modal } from 'antd';
import { SearchOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import { ToastContainer } from 'react-toastify';
import { httpClient } from '../../../util/Api';
import { baseUrlImage } from '../../../config/backend';
import { notificaciones } from '../../../util/util';
import ModalAgregar from './modalAgregar';

const ConfiguracionFirmas = () => {
  //TODO: crud de firmas como la asignación (busqueda)

  const [data, setData] = useState([]);
  const [abrirModal, setAbrirModal] = useState(false);
  const [loadingData, setLoadingData] = useState(false);
  const [filaActual, setFilaActual] = useState(null);

  const getColumnSearchProps = dataIndex => ({
    filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
      <div style={{ padding: 8 }}>
        <Input
          placeholder={`Buscar ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Buscar
          </Button>
          <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
            Reiniciar
          </Button>
        </Space>
      </div>
    ),
    filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase())
        : '',
  });

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
  };

  const handleReset = clearFilters => {
    clearFilters();
  };

  const columns = [
    {
      title: "Firma",
      dataIndex: "url_firma",
      key: "url_firma",
      width: 100,
      render: (url_firma) => (
        <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', gap: '20px' }}>
          <Avatar
            size="large"
            shape="square"
            icon={
              <Image
                preview={{
                  mask: (<div style={{ height: '50px' }}></div>)
                }}
                style={{ width: '100%', height: '50px', objectFit: 'cover' }}
                src={`${baseUrlImage}/${url_firma}`}
                fallback="data:image/png;base64,"
              />
            }
          />
        </div >
      ),
    },
    {
      title: 'Cod. Médico',
      dataIndex: 'cod_med',
      key: 'cod_med',
      ...getColumnSearchProps('cod_med'),
    },
    {
      title: 'Médico',
      dataIndex: 'nombres',
      key: 'nombres',
      render: (text, record) => (
        <>{record.nombres} {record.apellidos}</>
      )
    },
    {
      title: 'Fecha firma',
      dataIndex: 'fecha_firma',
      key: 'fecha_firma',
    },
    {
      title: 'URL firma',
      dataIndex: 'url_firma',
      key: 'url_firma',
    },
    {
      title: 'Acciones',
      dataIndex: 'a',
      // width: 80,
      render: (text, record) => (
        <span>
          <span className="gx-link">
            <i
              className="icon icon-edit"
              style={{ fontSize: 16, color: 'orange' }}
              onClick={() => {
                setFilaActual(record);
                setAbrirModal(true);
              }}
            />
          </span>
          <Divider type="vertical" />
          <span className="gx-link">
            <i
              className="icon icon-trash"
              style={{ fontSize: 17, color: "red" }}
              onClick={() => {
                confirm(record);
              }}
            />
          </span>
        </span >
      ),
    }
  ];

  const traerData = async () => {
    setLoadingData(true);
    const { data: { data = []} } = await httpClient.get('firmas/getFirmas');
    setData(data.map((item, key) => {
      return {
        ...item,
        key
      }
    }));
    setLoadingData(false);
  };

  const eliminarFirma = async (body) => {
    const repuesta = await httpClient.post('firmas/deleteFirma', body);
    return repuesta.data;
  };

  const confirm = (record) => {
    Modal.confirm({
      title: '¿Desea eliminar este registro?',
      icon: <ExclamationCircleOutlined />,
      okText: 'Si, Eliminar Registro',
      cancelText: 'No, Cancelar',
      onOk: async () => {
        var respuesta = await eliminarFirma({ cod_med: record.cod_med });
        if (respuesta.success) {
          await traerData();
          notificaciones('Completado!');
        } else {
          notificaciones(respuesta.message, 'Alerta');
        }
      },
      onCancel: () => {
        return false;
      }
    });
  }

  useEffect(() => {
    traerData();
  }, []);

  return (
    <Card
      title={
        <div
          style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(2, 1fr)',
            gridTemplateRows: '1fr',
            gridColumnGap: '0px',
            gridRowGap: '0px',
            marginRight: '5%',
          }}
        >
          <div style={{ gridArea: '1 / 1 / 2 / 2', fontSize: '22px', paddingTop: '20px' }}>
            Firmas
          </div>
          <div
            style={{
              gridArea: '1 / 2 / 2 / 3',
              display: 'flex',
              flexDirection: 'row-reverse',
              paddingTop: '15px',
            }}
          >
            <Button
              onClick={() => {
                setFilaActual(null);
                setAbrirModal(true);
              }}
              style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
              }}
              type="primary"
            >
              Agregar Firma
            </Button>
          </div>
        </div>
      }
    >
      <Table
        className="gx-table-responsive"
        columns={columns}
        dataSource={data}
        loading={loadingData}
      />
      {abrirModal ? (
        <ModalAgregar
          abrirModal={abrirModal}
          setAbrirModal={setAbrirModal}
          filaActual={filaActual}
          traerData={traerData}
        />
      ) : null}
      <ToastContainer pauseOnHover={false} />
    </Card>
  );
};

export default ConfiguracionFirmas;
